.c-footer {
  display: flex;
  justify-content: space-between;
  height: 72px;
  padding: 15px 120px;
  background-color: #383737; }
  @media only screen and (min-width: 769px) and (max-width: 1024px) {
    .c-footer {
      padding: 15px 60px; } }
  @media only screen and (min-width: 376px) and (max-width: 768px) {
    .c-footer {
      flex-direction: column;
      align-items: center;
      height: 150px;
      padding: 15px 30px; } }
  @media only screen and (max-width: 375px) {
    .c-footer {
      flex-direction: column;
      align-items: center;
      height: 200px;
      padding: 15px; } }
  .c-footer__menu {
    display: inline-block; }
    @media only screen and (min-width: 376px) and (max-width: 768px) {
      .c-footer__menu {
        text-align: center; } }
    @media only screen and (max-width: 375px) {
      .c-footer__menu {
        text-align: center; } }
    .c-footer__menu-item {
      color: #fff;
      font-size: 16px;
      line-height: 2.25;
      letter-spacing: 0.02rem;
      text-decoration: none; }
      .c-footer__menu-item:not(:last-child) {
        margin-right: 30px; }
      .c-footer__menu-item:hover {
        text-decoration: underline; }
  .c-footer__copyright {
    display: inline-block;
    color: #838282;
    font-size: 16px;
    line-height: 2.25;
    letter-spacing: 0.02rem; }
  .c-footer .c-social {
    display: inline-block; }
    @media only screen and (min-width: 376px) and (max-width: 768px) {
      .c-footer .c-social {
        display: flex;
        flex-flow: wrap;
        justify-content: center; } }
    @media only screen and (max-width: 375px) {
      .c-footer .c-social {
        display: flex;
        flex-flow: wrap;
        justify-content: center; } }
    .c-footer .c-social-buttons {
      display: inline-block; }
    .c-footer .c-social-item {
      padding: 9px;
      border: none;
      border: 1px solid transparent;
      background-color: transparent;
      outline: none;
      color: #9c9b9b;
      font-size: 14px;
      text-decoration: none; }
      .c-footer .c-social-item:hover {
        border-color: #9c9b9b;
        border-radius: 999rem; }
